(function() {
  'use strict';

  angular
    .module('teste')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main',
        resolve:{
          availability: function(AvailabilityService) {
            return AvailabilityService.get();
          }
        }
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

